.information {
  padding-top: 26px;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 648px;
    padding-top: 4px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
  }
}

.information__intro {
  margin-right: 20px;
  margin-left: 20px;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding-top: 27px;
  }
}

.information__contacts {
  margin-right: 20px;
  margin-left: 20px;

  @media (min-width: @tablet-width) {
    padding-top: 26px;
  }
}

.information__list {
  .list();
  display: flex;
  gap: 16px;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    width: 280px;
  }

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}

.information__item {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;

  @media (min-width: @tablet-width) {
    width: 280px;
  }

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}

.information__item--middle-name {
  padding-bottom: 39px;
}

.information__item--phone,
.information__item--email {
  position: relative;

  @media (min-width: @tablet-width) {
    padding-left: 20px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 47px;
    width: 402px;
  }
}

