.information__label {
  display: block;
  padding-left: 32px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    padding-left: 16px;
  }
}

.information__input {
  display: block;
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  padding: 16px 68px 16px 16px;
  background-color: #f2f2f2;
  border: none;
  margin: 0;
  outline: none;

  &:hover {
    background-color: #e5e5e5;
  }

  &:active,
  &:focus {
    background-color: @white;
    outline: 4px solid #e5e5e5;
    outline-offset: -4px;
  }

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }
}

.information__input--contacts {
  &[type="tel"] {
    background-image: url("../img/icons/phone-icon.svg");
    background-repeat: no-repeat;
    background-position: right;
  }

  &[type="email"] {
    background-image: url("../img/icons/email-icon.svg");
    background-repeat: no-repeat;
    background-position: right;
  }
}
